'use client'

import React, { createContext, useContext, useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { api, User } from '@/lib/api'

interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string) => Promise<void>
  register: (data: {
    email: string;
    full_name: string;
    password: string;
    role: 'etudiant' | 'enseignant' | 'admin';
  }) => Promise<void>
  logout: () => void
  updateUser: (user: User) => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const router = useRouter()

  useEffect(() => {
    checkAuth()
  }, [])

  const checkAuth = async () => {
    try {
      const token = localStorage.getItem('access_token')
      
      if (!token) {
        console.log('No token found')
        setIsLoading(false)
        return
      }

      console.log('Checking authentication...')
      const userData = await api.getCurrentUser()
      console.log('User authenticated:', userData)
      setUser(userData)
      
      // ‚úÖ REDIRECTION IMM√âDIATE SI D√âJ√Ä CONNECT√â
      if (typeof window !== 'undefined') {
        const currentPath = window.location.pathname
        const isOnAuthPage = ['/login', '/register', '/forgot-password', '/'].includes(currentPath)
        
        if (isOnAuthPage) {
          console.log('Already logged in, redirecting to dashboard...')
          // Forcer la redirection IMM√âDIATEMENT
          window.location.href = '/dashboard'
        }
      }
      
    } catch (error: any) {
      console.error('Auth check failed:', error.message)
      localStorage.removeItem('access_token')
    } finally {
      setIsLoading(false)
    }
  }

  const login = async (email: string, password: string) => {
    setIsLoading(true)
    try {
      console.log('Logging in with:', email)
      
      const response = await api.login(email, password)
      console.log('Login response:', response)
      
      // Stocker le token
      localStorage.setItem('access_token', response.access_token)
      console.log('Token stored')
      
      // R√©cup√©rer les infos utilisateur
      const userData = await api.getCurrentUser()
      console.log('User data:', userData)
      setUser(userData)
      
      // ‚úÖ FORCER LA REDIRECTION avec window.location
      console.log('Redirecting to dashboard...')
      window.location.href = '/dashboard'
      
    } catch (error: any) {
      console.error('Login failed:', error)
      throw new Error(error.message || '√âchec de la connexion. V√©rifiez vos identifiants.')
    } finally {
      setIsLoading(false)
    }
  }

  const register = async (data: {
    email: string;
    full_name: string;
    password: string;
    role: 'etudiant' | 'enseignant' | 'admin';
  }) => {
    setIsLoading(true)
    try {
      console.log('Registering:', data.email)
      
      const userData = await api.register(data)
      console.log('Registration successful:', userData)
      setUser(userData)
      
      // Auto-login apr√®s inscription
      const loginResponse = await api.login(data.email, data.password)
      localStorage.setItem('access_token', loginResponse.access_token)
      
      // Redirection FORC√âE
      window.location.href = '/dashboard'
    } catch (error: any) {
      console.error('Registration failed:', error)
      throw new Error(error.message || '√âchec de l\'inscription.')
    } finally {
      setIsLoading(false)
    }
  }

  const logout = () => {
    console.log('Logging out...')
    localStorage.removeItem('access_token')
    setUser(null)
    // Forcer la redirection
    window.location.href = '/login'
  }

  const updateUser = (updatedUser: User) => {
    setUser(updatedUser)
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        isLoading,
        login,
        register,
        logout,
        updateUser,
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}